@import url(https://fonts.googleapis.com/css?family=Karla:400,700&display=swap);
:root {
  --primary-dark: rgba(55, 71, 79, 1);
  --primary-light: rgba(96, 125, 139, 1);
  --primary-blue: rgba(179, 229, 252, 1);
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background: rgba(38, 50, 56, 1);
  color: white;
  font-family: "Karla", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Responsive */

@media screen and (max-width: 992px) {
  html {
    font-size: 12px;
  }
  div.market-whale {
    margin: 4rem 2rem 2rem;
  }
  .market-whale > div {
    max-height: 100vh;
    padding: 1rem;
  }

  .market-whale div.menu .single-item img {
    width: 70px;
  }
  .market-whale div.menu .single-item h3 {
    font-size: 1rem;
  }
  .market-whale div.menu .single-item h3 span.price {
    margin-left: 5px;
  }
  .market-whale div.menu .add-to-cart[disabled] {
    -webkit-transform: rotate(-10deg) scale(1.5) translateX(20%) translateY(-70%);
            transform: rotate(-10deg) scale(1.5) translateX(20%) translateY(-70%);
  }
  .market-whale div.menu .header h1 {
    font-size: 2rem;
  }
  .market-whale div.menu .header h1 img {
    width: 20px;
  }

  .market-whale div.order {
    flex-basis: 40%;
  }

  .market-whale div.inventory textarea {
    height: 100px;
  }
}

@media screen and (max-width: 992px) {
  /* .market-whale div.menu {
    flex-basis: 60%;
  }
  .market-whale div.order {
    flex-basis: 40%;
  } */
}

/* Responsive*/

form.store-selector {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  margin: 50px auto;
  max-width: 500px;
  padding: 2rem;
  background: var(--primary-dark);
  border: 1px solid var(--primary-light);
  border-radius: 3px;
}
form.store-selector > * {
  font-family: "Karla", sans-serif;
  text-align: center;
}
.store-selector h2 {
  margin-top: 0px;
}
.store-selector input[type="text"] {
  font-size: 2rem;
  padding: 5px 0px;
  margin-bottom: 5px;
  background: white;
  border: 1px solid var(--primary-dark);
  color: var(--primary-light);
}
.store-selector input[type="text"]::-webkit-input-placeholder {
  color: rgba(55, 71, 79, 0.2);
}
.store-selector input[type="text"]::-moz-placeholder {
  color: rgba(55, 71, 79, 0.2);
}
.store-selector input[type="text"]:-ms-input-placeholder {
  color: rgba(55, 71, 79, 0.2);
}
.store-selector input[type="text"]::-ms-input-placeholder {
  color: rgba(55, 71, 79, 0.2);
}
.store-selector input[type="text"]::placeholder {
  color: rgba(55, 71, 79, 0.2);
}
.store-selector button {
  padding: 5px 0;
  color: white;
  border: 1px solid var(--primary-light);
  background: var(--primary-dark);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.store-selector button:hover {
  cursor: pointer;
  background: var(--primary-light);
}

.header {
  border-bottom: 1px solid rgba(179, 229, 252, 0.2);
  padding-bottom: 2rem;
}
.header h1 {
  font-size: 2.5rem;
  margin: 0rem auto 0.25rem;
}
.header h1 span {
  color: var(--primary-blue);
}
.header img {
  position: relative;
  top: 4px;
}
.header .tagline {
  font-weight: normal;
  margin: 0 auto;
  font-size: 1rem;
  padding: 5px 10px;
  background: var(--primary-dark);
  border-radius: 3px;
}

/* Animation */
.order ul.order-list li.order-enter {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  max-height: 0;
  padding: 0;
  opacity: 0;
  -webkit-transform: translateY(10%);
          transform: translateY(10%);
}
.order ul.order-list li.order-enter.order-enter-active {
  max-height: 60px;
  opacity: 1;
  padding: 5px 10px;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.order ul.order-list li.order-exit {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.order ul.order-list li.order-exit.order-exit-active {
  -webkit-transform: translateY(20%);
          transform: translateY(20%);
  opacity: 0;
}

/* Count animation */

span.order-count {
  overflow: hidden;
  display: flex;
  justify-content: center;
}
span.count {
  position: relative;
  float: left;
}
span.count span {
  display: inline-block;
}
.count-enter {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.count-enter.count-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.count-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  position: absolute;
  left: 0;
  bottom: 0;
}

.count-exit.count-exit-active {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

/* End-Count animation */

/* End-Animation */

.order {
  overflow: hidden;
}
.order h1 {
  font-size: 2.5rem;
  margin-top: 0;
  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(179, 229, 252, 0.2);
}
.order ul.order-list {
  list-style: none;
  padding: 0;
}
.order ul.order-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  background: var(--primary-dark);
  margin-bottom: 1rem;
  position: relative;
}
.order ul.order-list li span.order-count {
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 3px;
  background: rgba(38, 50, 56, 1);
  white-space: nowrap;
}
.order ul.order-list li span.order-price {
  padding: 2px 4px;
  border-radius: 3px;
  background: rgba(38, 50, 56, 1);
  font-weight: bold;
}

.order ul.order-list li span.order-name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0 10px;
}

.order .total {
  padding: 1rem;
  font-size: 1.5rem;
  background: var(--primary-blue);
  color: rgba(38, 50, 56, 1);
  font-weight: bold;
}
.order .order-delete {
  background: rgba(255, 23, 68, 0.5);
  border: none;
  color: white;
  padding: 4px 0px;
  position: absolute;
  right: 10px;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  width: 50px;
  border-radius: 3px;
  display: none;
  opacity: 0;
}
.order .order-delete:hover {
  cursor: pointer;
}
.order ul.order-list li:hover .order-delete {
  display: flex;
  opacity: 1;
}
.order ul.order-list li:hover {
  cursor: pointer;
}
.order ul.order-list li:hover .order-price {
  opacity: 0;
}

