  background: var(--primary-dark);
  border-radius: 3px;
}
.item-add textarea:focus,
.item-add input:focus,
.item-add select:focus,
.item-add button:focus {
  outline: none;
  border-color: var(--primary-blue);
}

.item-add > * {
  font-family: "Karla", sans-serif;
  padding: 10px;
  color: white;
  border: 1px solid var(--primary-light);
  background: var(--primary-dark);
  margin-bottom: 1rem;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.item-add > *:hover {
  background: var(--primary-light);
}

.item-add select,
.item-add button {
  font-weight: bold;
}
.item-add input::-webkit-input-placeholder,
.item-add textarea::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.item-add input::-moz-placeholder,
.item-add textarea::-moz-placeholder {
