.inventory {
  display: flex;
  flex-direction: column;
}
.inventory h1 {
  font-size: 2.5rem;
  margin-top: 0;

  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(179, 229, 252, 0.2);
}
.inventory button.load-button {
  font-family: "Karla", sans-serif;
  margin-top: 1rem;
  width: 33.33%;
  padding: 10px;
  color: white;
  font-weight: bold;
  border: 1px solid var(--primary-light);
  background: var(--primary-dark);
  margin-bottom: 1rem;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.inventory button.load-button:focus {
  outline: none;
  border-color: var(--primary-blue);
}
.inventory button:hover {
  cursor: pointer;
  background: var(--primary-light);
}
button.logout {
  cursor: pointer;
  font-family: "Karla", sans-serif;
  margin: 1rem 0;
  padding: 5px;
  color: white;
  font-weight: bold;
  border: 1px solid var(--primary-light);
  background: var(--primary-dark);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
button.logout:hover {
  background: rgba(244, 67, 54, 1);
  border: 1px solid rgba(244, 67, 54, 1);
}

.list-of-items {
  list-style: none;
  padding: 0;
  margin-top: 2rem;
}
.single-item {
  padding: 1rem 1rem 1rem 0;
  background: var(--primary-dark);
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  margin-bottom: 5px;
  border: 1px solid transparent;
  clear: both;
  overflow: hidden;
}
.single-item:not(.disabled):hover {
  cursor: pointer;
  border: 1px solid var(--primary-light);
  box-sizing: border-box;
}
.single-item:not(:last-child) {
  margin-bottom: 1rem;
}
.single-item img {
  position: relative;
  width: 150px;
  float: left;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top: 1px solid var(--primary-light);
  border-bottom: 1px solid var(--primary-light);
  border-right: 1px solid var(--primary-light);
  z-index: 5;
}
.single-item h3 {
  position: relative;
  right: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  font-size: 1.5rem;
  padding: 0.5rem 1rem;
  background: rgba(38, 50, 56, 1);
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  z-index: 1;
}
.single-item h3 span.price {
  font-size: 1rem;
  padding: 2px 4px;
  background: var(--primary-dark);
  border-radius: 3px;
}
.single-item p.desc {
  padding-left: calc(1rem - 5px);
  margin: 0.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.single-item button.add-to-cart {
  font-family: "Karla", sans-serif;
  margin-left: calc(1rem - 5px);
  padding: 5px;
  color: white;
  font-weight: bold;
  border: 1px solid var(--primary-light);
  background: var(--primary-dark);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.single-item button.add-to-cart:focus {
  outline: none;
  border-color: var(--primary-blue);
}

.single-item button.add-to-cart:hover {
  cursor: pointer;
  background: var(--primary-light);
}
.single-item.disabled {
  cursor: not-allowed;
}
.single-item.disabled > *:not(button) {
  /* opacity: 0.2; */
  cursor: not-allowed;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-filter: blur(0.8px);
          filter: blur(0.8px);
  opacity: 0.5;
}

.single-item button.add-to-cart[disabled] {
  cursor: not-allowed;
  color: white;
  background: rgba(244, 67, 54, 1);
  border: none;
  padding: 2px;
  z-index: 999;
  position: relative;
  -webkit-transform: rotate(-10deg) scale(2) translateX(50%) translateY(-50%);
          transform: rotate(-10deg) scale(2) translateX(50%) translateY(-50%);
}

.market-whale {
  display: flex;
  height: 90vh;
  max-width: 1500px;
  margin: 5vh auto;
  perspective: 1000;
  -webkit-perspective: 1000;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
/* .market-whale > * {
  overflow-y: scroll;
} */
button.fold {
  position: absolute;
  cursor: pointer;
  top: 10px;
  left: 10px;
  z-index: 999;
  padding: 5px 15px;
  color: var(--primary-blue);
  background: var(--primary-dark);
  border: 2px solid var(--primary-blue);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
button.fold.folded {
  background: var(--primary-blue);
  color: var(--primary-dark);
}

.market-whale > * {
  flex: 1 1 auto;
  padding: 2rem;
  border: 1px solid var(--primary-light);
  position: relative;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  overflow-y: scroll;
}

.market-whale > *::-webkit-scrollbar-track {
  background-color: rgba(38, 50, 56, 1);
}

.market-whale > *::-webkit-scrollbar {
  width: 5px;
}

.market-whale > *::-webkit-scrollbar-thumb {
  border-radius: 13px;
  background: rgba(255, 255, 255, 0.1);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  position: relative;
  margin-left: 2rem;
}

.market-whale > :first-child {
  flex-basis: 40%;
  border-bottom-left-radius: 6px;
  border-top-left-radius: 6px;
  left: 1px;
}
.market-whale.folded > :first-child {
  -webkit-transform: translateX(50%) rotateY(6deg) translateX(-50%);
          transform: translateX(50%) rotateY(6deg) translateX(-50%);
}

.market-whale > :nth-child(2) {
  flex-basis: 20%;
}
.market-whale.folded > :nth-child(2) {
  -webkit-transform: translateX(-50%) rotateY(-14deg) translateX(50%);
          transform: translateX(-50%) rotateY(-14deg) translateX(50%);
}

.market-whale > :last-child {
  flex-basis: 40%;
  border-bottom-right-radius: 6px;
  border-top-right-radius: 6px;
  right: 1px;
}
.market-whale.folded > :last-child {
  -webkit-transform: translateX(-50%) rotateY(10deg) translateX(50%) scale(1.08)
    translateX(24px);
          transform: translateX(-50%) rotateY(10deg) translateX(50%) scale(1.08)
    translateX(24px);
}
.built-by,
.built-by:focus,
.built-by:active {
  font-family: "Karla", sans-serif;
  position: absolute;
  cursor: pointer;
  top: 10px;
  left: 100px;
  z-index: 999;
  padding: 5px 15px;
  color: var(--primary-blue);
  background: var(--primary-dark);
  border: 1px solid var(--primary-dark);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.built-by a {
  color: inherit;
  text-decoration: none;
}
.built-by:hover {
  color: white;
  border: 1px solid var(--primary-light);
}

.not-found {
  margin: 3rem auto;
  display: flex;
  justify-content: center;
  color: white;
}

